import Base from "@//utils/base";

/**
 *  html
 *  @param str string
 */
export const EscapeHTML = (str: string) => {
  let result = "";
  result = str.replace("&", "&amp;");
  result = str.replace("'", "&#x27;");
  result = str.replace("`", "&#x60;");
  result = str.replace('"', "&quot;");
  result = str.replace("<", "&lt;");
  result = str.replace(">", "&gt;");
  result = str.replace(/\n/, "<br>");

  return result;
};

/**
 *  serialize
 *  @param data object
 */
export const Serialize = (data: object) => {
  let key: string;
  let value: string;
  let type: string;
  let i: number;
  let max: number;
  const encode = window.encodeURIComponent;
  let query: string = "?";

  for (key of Object.keys(data)) {
    value = data[key];
    type = typeof value === "object" && value instanceof Array ? "array" : typeof value;
    switch (type) {
      case "undefined":
        // 
        query += key;
        break;
      case "array":
        // 
        for (i = 0, max = value.length; i < max; i++) {
          query += key + "[]";
          query += "=";
          query += encode(value[i]);
          query += "&";
        }
        query = query.substr(0, query.length - 1);
        break;
      case "object":
        // 
        for (i of Object.keys(value)) {
          query += key + "[" + i + "]";
          query += "=";
          query += encode(value[i]);
          query += "&";
        }
        query = query.substr(0, query.length - 1);
        break;
      default:
        query += key;
        query += "=";
        query += encode(value);
        break;
    }
    query += "&";
  }
  query = query.substr(0, query.length - 1);
  return query;
};

/**
 *  
 */
export const GetUrlVars = () => {
  const vars: object = {};
  const url = Base._w.location.search;
  // ?1&
  const hash: string[] = url.slice(1).split("&");
  const max: number = hash.length;
  for (let i = 0; i < max; i++) {
    const array: string[] = hash[i].split("="); // key
    // vars.push(array[0]);    // key
    if (array[0] !== "") {
      vars[array[0]] = array[1]; // key
    }
  }
  return vars;
};
